import React from "react";

import { Button, Paragraf, Headder, Seksjon, Kilde } from "./styles";

const Mars = () => {
  return (
    <Seksjon>
      <div className='container mars__container'>
        <article>
          <Headder>Mars</Headder>

          <Paragraf>
            Mars er den fjerde planeten regnet fra Sola. Den er den syvende
            strste planeten i solsystemet og har litt over halvparten s stor
            omkrets som Jorda. Den er i likhet med Merkur, Venus og Jorda en
            steinplanet. Et dgn p Mars varer 24,6 timer, ikke ulikt et dgn p
            Jorda (23,9 timer). Et Mars-dgn kalles sol, som er en forkortelse
            for solar day (soldgn). Mars kan ha vrt mer jordlignende for 3,5
            milliarder r siden. Formasjoner p overflaten tyder p at vann har
            rent der tidligere, mens i dag er overflaten trrlagt.{" "}
            <Kilde>
              Aksnes, Kaare; Hammerstrm, Maria: Mars i Store norske leksikon p
              snl.no. Hentet 11. oktober 2022 fra
              <Button href='https://snl.no/Mars'>SNL</Button>
            </Kilde>
          </Paragraf>
        </article>
      </div>
    </Seksjon>
  );
};

export default Mars;
